import React from 'react';

import { Form, Button, InputGroup } from 'react-bootstrap';

export default class PasswordInput extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isVisible: false
    };
  };

  _displayEye = () => {
    return this.state.isVisible ? <i class="fas fa-eye-slash"></i> : <i class="fas fa-eye"></i>;
  };

  _displayType = () => {
    return this.state.isVisible ? 'text' : 'password';
  };

  _handleEye = () => {
    this.setState({ isVisible: !this.state.isVisible });
  };

  _displayContraint = () => {
    return this.props.contraint ?
      'Doit contenir des majuscules, contentir des minuscules, avoir au moins 8 caractères et avoir au moins 2 chiffres'
      :
      this.props.forUpdate ? "Laissez vide si vous ne voulez pas le changer" : "Le mot de passe..."
  }

  render() {
    return (
      <Form.Group controlId="formBasicPassword">
        <InputGroup className="mb-3">
          <Form.Control
            type={this._displayType()}
            placeholder={this._displayContraint()}
            aria-label="Mot de passe"
            aria-describedby={this.props.btnId}
            onChange={this.props.onChange}
            value= {this.props.value}
          />
          <InputGroup.Append>
            <Button variant="secondary" aria-hidden="true" onClick={this._handleEye}>{this._displayEye()}</Button>
          </InputGroup.Append>
        </InputGroup>
      </Form.Group>
    );
  }
}

PasswordInput.defautProps = {
  contraint: false
};