import url from './url';

export function forgotPassword(email) {
  const init = {
    method: 'POST',
    body: JSON.stringify({email: email}),
    headers: {
      "Content-type": "application/json; charset=UTF-8"
    },
  };
  return fetch(`${url}/users/forgot-password`, init)
    .then(res => res.json());
}

export function resettPassword(idUser, token, password) {
  const init = {
    method: 'POST',
    body: JSON.stringify({password: password}),
    headers: new Headers({
      'Authorization': 'Bearer '+token, 
      "Content-type": "application/json; charset=UTF-8"
    })
  };
  return fetch(`${url}/users/reset-password/${idUser}`, init)
    .then(res => res.json());
}