import { useState } from 'react';

import { Card, Form, Button, Spinner, Alert } from 'react-bootstrap';
import NavBar from '../Components/NavBar';
import PasswordInput from '../Components/PasswordInput';
import passwordValidator from 'password-validator';
import { resettPassword } from '../Services/users';

const ResetPassword = (props) => {
  const [password, setPassword] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  
  const idUser = props.match.params.idUser;
  const token = props.match.params.token;

  document.title = "Tech'Eat Fast - Mot de passe oublié";

  const onPasswordChange = (event) => {
    setPassword(event.target.value);
  }

  const onClick = (event) => {
    event.preventDefault();
    resettPassword(idUser, token, password)
      .then(data => {
        console.log(data);
      })
      .catch(error => {
        console.log(error);
      });
  };

  const displayButton = () => {
    const schema = new passwordValidator();// On crée une nouvelle instance de l'objet
    schema// On crée un nouveau schéma
      .is().min(8)                                    // Minimum length 8
      .has().uppercase()                              // Must have uppercase letters
      .has().lowercase()                              // Must have lowercase letters
      .has().digits(2)                                // Must have at least 2 digits
    if (schema.validate(password)) {
      return (
        <Button variant="success" type="submit" size="lg" disabled={isLoading} block onClick={onClick}>
          {isLoading ? <Spinner animation="border" variant="light" style={{ width: 25, height: 25, fontSize: '75%' }} /> : "Confirmer"}
        </Button>
      );
    } else {
      return (
        <Button variant="success" size="lg" disabled={true} block>
          Confirmer
        </Button>
      );
    }
  }

  return (
    <>
      <NavBar />
      <div className='container' style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', flexDirection: 'column' }}>
        <br />
        <h1>Mot de passe oublié</h1>
        <br />
        <Card style={{ width: '20rem' }}>
          <Card.Body>
            <Card.Title style={{ textAlign: 'center', }}>Changer votre mot de passe</Card.Title>
            <Card.Text style={{ fontSize: "80%" }}>Doit contenir des majuscules, contentir des minuscules, avoir au moins 8 caractères et avoir au moins 2 chiffres</Card.Text>
            <Form>
              <Form.Group controlId="password">
                <Form.Label>Nouveau mot de passe: </Form.Label>
                <PasswordInput value={password} onChange={onPasswordChange} />
              </Form.Group>
              {displayButton()}
            </Form>
          </Card.Body>
        </Card>
      </div>
    </>
  );
}

export default ResetPassword;