import { useState } from 'react';

import { Card, Form, Button, Spinner, Alert } from 'react-bootstrap';
import NavBar from '../Components/NavBar';
import validator from 'validator';
import { forgotPassword } from '../Services/users';

const ForgotPassword = () => {
  const [email, setEmail] = useState("");

  const [isLoading, setIsLoading] = useState(false);
  const [success, setSuccess] = useState(false);
  const [invalidEmail, setInvalidEmail] = useState(false);
  const [error, setError] = useState(false);

  document.title = "Tech'Eat Fast - Mot de passe oublié";

  const onEmailChange = (event) => {
    setEmail(event.target.value);
  }

  const onClick = (event) => {
    event.preventDefault();
    setIsLoading(true);
    forgotPassword(email)
      .then(data => {
        if (data.emailSend) {
          setError(false);
          setInvalidEmail(false);
          setSuccess(true);

          setIsLoading(false);
        } else if (data.invalidEmail) {
          setError(false);
          setInvalidEmail(true);
          setSuccess(false);

          setIsLoading(false);
        } else {
          setError(true);
          setInvalidEmail(false);
          setSuccess(false);

          setIsLoading(false);
        }
      })
      .catch(() => {
        setError(true);
        setInvalidEmail(false);
        setSuccess(false);

        setIsLoading(false);
      });
  }

  const displayButton = () => {
    if (validator.isEmail(email)) {
      return (
        <Button variant="success" type="submit" size="lg" disabled={isLoading} block onClick={onClick}>
          {isLoading ? <Spinner animation="border" variant="light" style={{ width: 25, height: 25, fontSize: '75%' }} /> : "Confirmer"}
        </Button>
      );
    } else {
      return (
        <Button variant="success" size="lg" disabled={true} block>
          Confirmer
        </Button>
      );
    }
  }

  if (success) {
    return (
      <>
        <NavBar />
        <div className='container' style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', flexDirection: 'column' }}>
          <br />
          <h1>Mot de passe oublié</h1>
          <br />
          <Card style={{ width: '20rem' }}>
            <Card.Body>
              <Card.Title style={{ textAlign: 'center', }}>Récupérer votre compte</Card.Title>
              <Card.Text style={{ fontSize: "90%" }}>
                Un email vient de vous être envoyé avec un lien pour récupérer votre compte. Il expire dans 15 minutes
              </Card.Text>
            </Card.Body>
          </Card>
        </div>
      </>
    );
  } else {
    return (
      <>
        <NavBar />
        <div className='container' style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', flexDirection: 'column' }}>
          <br />
          <h1>Mot de passe oublié</h1>
          <br />
          <Card style={{ width: '20rem' }}>
            <Card.Body>
              <Card.Title style={{ textAlign: 'center', }}>Récupérer votre compte</Card.Title>
              <Card.Text style={{ fontSize: "80%" }}>
                Entrez votre adresse e-mail et nous vous enverrons un lien pour récupérer votre compte.
              </Card.Text>
              <Alert variant="danger" style={{ fontSize: "80%" }} show={error}>
                Une erreur a eu lieu lors de la communication avec le serveur, veuillez réessayer plus tard ou contactez-nous.
              </Alert>
              <Alert variant="warning" style={{ fontSize: "80%" }} show={invalidEmail}>
                Votre adresse email est introuvable, êtes vous sûre que c'est la bonne?
              </Alert>
              <Form>
                <Form.Group controlId="email">
                  <Form.Label>Adresse email: </Form.Label>
                  <Form.Control type="email" placeholder="Votre adresse email (ex: john@example.com)..." value={email} onChange={onEmailChange} />
                </Form.Group>
                {displayButton()}
              </Form>
            </Card.Body>
          </Card>
        </div>
      </>
    );
  }
};

export default ForgotPassword;